<template>
  <div class="about">
    <h1>This is an about page</h1>
    <p>{{countNum}}</p>
    <p><input type="button" value="count+5" @click="addCount" /></p>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'about',
    computed: mapState({
      countNum(state) {
        return state.count
      }
    }),
    methods: {
      addCount () {
        this.$store.commit('incrementCount', 5);
      }
    }
  }
</script>
